<style lang="less" scoped>
  @import '../../styles/common.less';
  @import '../../styles/table.less';
</style>
<template>
  <div class="single-page-shadow">
    <Card shadow :dis-hover="true" >
      <p slot="title">
        <Icon type="md-settings"></Icon>
        用户配置
      </p>
      <Row>
        <Col span="7">
        <Card :dis-hover="true" class="show-shadow">
          <p slot="title">
            <Icon type="md-add"></Icon>
            添加用户
          </p>
          <div class="edittable-testauto-con">
            <Form :model="userinfo" ref="userinfova" :rules="userinfoValidate">
              <FormItem label="用户名" prop="username">
                <Input v-model="userinfo.username" placeholder="请输入"></Input>
              </FormItem>
              <FormItem label="密码" prop="password">
                <Input v-model="userinfo.password" placeholder="请输入" type="password"></Input>
              </FormItem>
              <FormItem label="确认密码" prop="confirmpassword">
                <Input v-model="userinfo.confirmpassword" placeholder="请输入" type="password"></Input>
              </FormItem>
              <FormItem label="部门" prop="department">
                <Input v-model="userinfo.department" placeholder="请输入"></Input>
              </FormItem>
              <FormItem label="姓名" prop="username_chn">
                <Input v-model="userinfo.username_chn" placeholder="请输入"></Input>
              </FormItem>
              <FormItem label="角色" prop="role">
                <Select v-model="userinfo.role" placeholder="请选择">
                  <Option value="admin">管理员</Option>
                  <Option value="guest">使用人</Option>
                </Select>
              </FormItem>
              <FormItem label="电子邮箱" prop="email">
                <Input v-model="userinfo.email" placeholder="请输入"></Input>
              </FormItem>
              <Button type="primary" @click.native="registered" style="margin-left: 35%" :loading="addLoading">添加</Button>
            </Form>
          </div>
        </Card>
        </Col>
        <Col span="17" class="padding-left-10">
        <Card :dis-hover="true" class="show-shadow">
          <p slot="title">
            <Icon type="md-people"></Icon>
            系统用户表
          </p>
          <Input v-model="query.username" placeholder="请填写用户名" style="width: 20%" clearable></Input>
          <Input v-model="query.department" placeholder="请填写部门" style="width: 20%" clearable
                 class="margin-left-10"></Input>
          <Button @click="queryData" type="primary" class="margin-left-10">查询</Button>
          <Button @click="queryCancel" type="warning" class="margin-left-10">重置</Button>
          <div class="edittable-con-1">
            <Table border :columns="columns" :data="tableData" stripe height="520">
              <template slot-scope="{ row }" slot="action">
                <Button type="primary" size="small" @click="editPassModal(row)"
                        v-if="row.id !== 1">更改密码
                </Button>
                <Button type="info" size="small" @click="editAuthModal(row)" class="margin-left-10">详细信息</Button>
                <template v-if="row.Username !== 'admin'">
                  <Button @click="delUser(row)" type="warning" size="small" v-if="row.id !== 1" class="margin-left-10">删除</Button>
                </template>
              </template>
            </Table>
          </div>
          <br>
          <Page :total="totalNumber" show-elevator @on-change="pageChange" :page-size="page_size" ref="page"></Page>
        </Card>
        </Col>
      </Row>
    </Card>
    <Modal v-model="editPasswordForm.modal" :closable='false' :mask-closable=false :width="500">
      <h3 slot="header" style="color:#2D8CF0">修改用户密码</h3>
      <Form ref="editPasswordForm" :model="editPasswordForm" :label-width="100" label-position="right"
            :rules="passwordValidate">
        <FormItem label="用户名">
          <Input v-model="editPasswordForm.username" readonly="readonly"></Input>
        </FormItem>
        <FormItem label="新密码" prop="newPass">
          <Input v-model="editPasswordForm.newPass" placeholder="请输入新密码，至少6位字符" type="password"></Input>
        </FormItem>
        <FormItem label="确认新密码" prop="rePass">
          <Input v-model="editPasswordForm.rePass" placeholder="请再次输入新密码" type="password"></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="text" @click="editPasswordForm.modal = false">取消</Button>
        <Button type="primary" @click="saveEditPass" :loading="savePassLoading">保存</Button>
      </div>
    </Modal>

    <Modal v-model="editAuthForm.modal" @on-ok="saveAuthInfo">
      <h3 slot="header" style="color:#2D8CF0">用户信息</h3>
      <Form ref="userUpdate" :model="editAuthForm" label-position="right">
        <FormItem label="用户名">
          <Input v-model="editAuthForm.username" readonly="readonly"></Input>
        </FormItem>
        <FormItem label="真实姓名">
          <Input v-model="editAuthForm.username_chn"></Input>
        </FormItem>
        <FormItem label="角色">
          <Select v-model="editAuthForm.role" placeholder="请选择">
            <Option value="admin">管理员</Option>
            <Option value="guest">使用者</Option>
          </Select>
        </FormItem>
        <FormItem label="部门">
          <Input v-model="editAuthForm.department" placeholder="请输入新部门"></Input>
        </FormItem>
        <FormItem label="E-mail">
          <Input v-model="editAuthForm.email" placeholder="请输入邮箱"></Input>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>
<script>
    import axios from 'axios'
    import '../../styles/tablesmargintop.css'
    import { getUserList, addUser, updateUser, deleteUser } from '@/api/settings'

    export default {
      data() {
        const valideRePassword = (rule, value, callback) => { // eslint-disable-line no-unused-vars
          if (value !== this.editPasswordForm.newPass) {
            callback(new Error('两次输入密码不一致'))
          } else {
            callback()
          }
        };
        const valideuserinfoPassword = (rule, value, callback) => {
          if (value !== this.userinfo.password) {
            callback(new Error('两次输入密码不一致'))
          } else {
            callback()
          }
        };
        const regExp_password = (rule, value, callback) => {
          let pPattern = /^.*(?=.{6,})(?=.*\d)(?=.*[A-Z])(?=.*[a-z]).*$/;
          if (!pPattern.test(this.userinfo.password)) {
            callback(new Error('至少1个大写字母,1个小写字母,1个数字'))
          } else {
            callback()
          }
        };
        return {
          query: {
            username: '',
            department: ''
          },
          loading: false,
          columns: [
            {
              title: '用户名',
              key: 'username',
            },
            {
              title: '角色',
              key: 'role',
            },
            {
              title: '姓名',
              key: 'username_chn',
            },
            {
              title: '部门',
              key: 'department',
            },
            {
              title: 'email',
              key: 'email',
            },
            {
              title: '操作',
              key: 'action',
              width: 250,
              align: 'center',
              slot: 'action'
            }
          ],
          tableData: [],
          // 新建用户
          userinfo: {
            username: '',
            password: '',
            confirmpassword: '',
            role: '',
            department: '',
            email: '',
            username_chn: '',
            tenant: sessionStorage.getItem('tenant_name')
          },
          userinfoValidate: {
            username: [{
              required: true,
              message: '请输入用户名',
              trigger: 'blur'
            }],
            password: [
              {
                required: true,
                message: '请输入密码',
                trigger: 'blur'
              },
              {
                min: 6,
                message: '请至少输入6个字符',
                trigger: 'blur'
              },
              {
                max: 32,
                message: '最多输入32个字符',
                trigger: 'blur'
              },
              {
                validator: regExp_password,
                trigger: 'blur'
              }
            ],
            confirmpassword: [
              {
                required: true,
                message: '请再次输入新密码',
                trigger: 'blur'
              },
              {
                validator: valideuserinfoPassword,
                trigger: 'blur'
              }
            ],
            role: [
              {
                required: true,
                message: '请输入权限',
                trigger: 'blur'
              }
            ],
            department: [
              {
                required: true,
                message: '请输入部门名称',
                trigger: 'blur'
              },
              {
                min: 2,
                message: '请至少输入6个字符',
                trigger: 'blur'
              },
              {
                max: 32,
                message: '最多输入32个字符',
                trigger: 'blur'
              }
            ],
            username_chn: [
              {
                required: true,
                message: '请输入姓名',
                trigger: 'blur'
              },
              {
                min: 2,
                message: '请至少输入2个字符',
                trigger: 'blur'
              },
              {
                max: 32,
                message: '最多输入32个字符',
                trigger: 'blur'
              }],
            email: [
              {
                required: true,
                message: '请输入工作邮箱',
                trigger: 'blur'
              },
              {
                min: 2,
                message: '请至少输入2个字符',
                trigger: 'blur'
              },
              {
                max: 32,
                message: '最多输入32个字符',
                trigger: 'blur'
              }]
          },
          // 更改密码遮罩层状态
          editPasswordModal: false,
          // 更改密码
          editPasswordForm: {
            username: '',
            newPass: '',
            rePass: '',
            modal: false
          },
          // 保存更改密码loding按钮状态
          savePassLoading: false,
          // 更改密码表单验证规则
          passwordValidate: {
            newPass: [{
              required: true,
              message: '请输入新密码',
              trigger: 'blur'
            },
              {
                min: 6,
                message: '请至少输入6个字符',
                trigger: 'blur'
              },
              {
                max: 32,
                message: '最多输入32个字符',
                trigger: 'blur'
              }
            ],
            rePass: [{
              required: true,
              message: '请再次输入新密码',
              trigger: 'blur'
            },
              {
                validator: valideRePassword,
                trigger: 'blur'
              }
            ]
          },
          // 更改部门及权限
          editAuthForm: {
            username: '',
            department: '',
            username_chn: '',
            role: '',
            email: '',
            modal: false
          },
          // 更改部门及权限遮罩层状态
          // 用户名
          username: '',
          totalNumber: 0,
          currentPage: 1,
          page_size: 15,
          tableLoading: false,
          addLoading: false,
          changePass: false
        }
      },
      methods: {
        editPassModal(row) {
          this.editPasswordForm = this.$config.sameMerge(this.editPasswordForm, row, this.editPasswordForm);
          this.editPasswordForm.modal = true;
        },
        editAuthModal(row) {
          this.editAuthForm = this.$config.sameMerge(this.editAuthForm, row, this.editAuthForm);
          this.editAuthForm.modal = true;
        },
        saveEditPass() {
          this.$refs['editPasswordForm'].validate((valid) => {
            if (valid) {
              this.editPasswordForm.modal = false;
              updateUser({username: this.editPasswordForm.username, password: this.editPasswordForm.newPass}).then(res => {
                if (res.data.status === 'success') {
                  this.$Message.success({
                    content: '用户信息已保存 ',
                    duration: 5,
                    closable: true
                  });
                  this.$refs['editPasswordForm'].resetFields()
                } else {
                  this.$Message.error({
                    content: '用户信息保存错误',
                    duration: 8,
                    closable: true
                  });
                }
              }).catch(e => {
                this.bus.$emit('errorHandler', e);
              }).finally(() => {
                this.getData()
              })
          }})
        },
        saveAuthInfo() {
          this.$refs['editPasswordForm'].validate((valid) => {
            if (valid) {
              this.editAuthForm.modal = false;
              updateUser(this.editAuthForm).then(res => {
                if (res.data.status === 'success') {
                  this.$Message.success({
                    content: '用户信息已保存 ',
                    duration: 5,
                    closable: true
                  });
                  this.$refs['userUpdate'].resetFields()
                } else {
                  this.$Message.error({
                    content: '用户信息保存错误',
                    duration: 8,
                    closable: true
                  });
                }
              }).catch(e => {
                console.log(e)
                this.bus.$emit('errorHandler', e);
              }).finally(() => {
                this.getData()
              })
            }}
          )
        },
        registered() {
          this.$refs['userinfova'].validate((valid) => {
            if (valid) {
              this.$Modal.confirm({
                title: '请确认是否添加新用户信息',
                'z-index': 1010,
                // content: '请确认创建Review任务',
                onOk: () => {
                  this.addLoading = true
                  addUser(this.userinfo).then(res => {
                    if (res.data.status === 'success') {
                      this.$Message.success({
                        content: '用户信息已经保存',
                        duration: 5,
                        closable: true
                      });
                      this.$refs['userinfova'].resetFields()
                    } else {
                      this.$Message.error({
                        content: '用户名重复或保存信息异常',
                        duration: 5,
                        closable: true
                      });
                    }
                  }).catch(e => {
                    this.bus.$emit('errorHandler', e);
                  }).finally(() => {
                    this.addLoading = false
                    this.getData()
                  })
                },
                onCancel: () => {
                  // this.$Message.info('Clicked cancel');
                }
              });
            }
          })
        },
        getData() {
          this.tableLoading = true;
          const limit = this.page_size
          const offset = this.page_size * (this.currentPage - 1)
          getUserList(limit, offset, this.query.username, this.query.department).then(res => {
            console.log(res)
            this.tableData = res.data.data
            this.totalNumber = res.data.count
          }).catch(e => {
            this.bus.$emit('errorHandler', e);
          }).finally(() => {
            this.tableLoading = false;
          })
        },
        delUser(row) {
          this.$Modal.confirm({
            title: '请确认是否删除用户信息',
            'z-index': 1010,
            onOk: () => {
              deleteUser(row.username).then(res => {
                if (res.data.status === 'success') {
                  this.$Message.success({
                    content: '用户信息已删除',
                    duration: 5,
                    closable: true
                  });
                } else {
                  this.$Message.error({
                    content: '删除用户信息异常',
                    duration: 5,
                    closable: true
                  });
                }
              }).catch(e => {
                this.bus.$emit('errorHandler', e);
              }).finally(() => {
                this.getData()
              })
            },
            onCancel: () => {
              // this.$Message.info('Clicked cancel');
            }
          });
        },
        queryData() {
          this.query.valve = true;
          this.getData()
        },
        queryCancel() {
          this.$config.clearObj(this.query)
          this.getData()
        },
        pageChange(p) {
          this.currentPage = p
          this.getData()
        }
      },
      mounted() {
        this.getData();
      }
    }
</script>
